/* Wrapper around switch */
.wrapper__switch {
  padding: 30px;
  background-color: white;

  --pillar--padding: 20px;
  --pillar__logo--width: 84px;
}

.wrapper__pillar {
  position: absolute;
  top: 0;
  left: calc(
    50% - calc(var(--pillar__logo--width) / 2) - var(--pillar--padding)
  );
}
.pillar {
  padding: var(--pillar--padding);
  padding-top: 60px;
  background-color: black;
}
.pillar__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}
.footer__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

/* classes to be used in components */
.component__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__background {
  width: 100%;
  background-color: black;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.2) 0%,
      rgba(0, 0, 0, 1) 75%
    ),
    url("/home/arnell/React/upgraded-umbrella/src/assets/images/bg.png");
  background-position: center;
  background-repeat: no-repeat;
  color: white;

  padding-top: 170px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__inner {
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.component__title h2 {
  letter-spacing: 2px;
  margin-bottom: 10px;
}
.component__title hr {
  width: 80%;
  margin-bottom: 30px;
}
.component__footer {
}
.component__wrapper h1 {
  width: 80%;
  letter-spacing: 3.5px;
  text-transform: uppercase;
}
.component__spacer {
  height: 60px;
}
